        .box {
            margin-right: 470px;
            white-space:nowrap
        }
    }
    .early-warning-router-body{
        display: flex;
        margin:160px 0 0 80px;
        .body-left{
            width:1065px;
            margin-right:128px;
            text-align: left;
            .body-row{
                font-size:28px;
                display: flex;
                line-height: 39px;
                margin-bottom:88px;
                .name{
                    color:#74F4F7;
                    margin-right:100px;
                }
                .content{
